      min-width: 180px;
      font-size: 11px;
      padding: 10px 12px;
    }
    .ide-full {
      font-size: 11px;
    }
    .ide-sidebar {
      display: none;
    }
    .ide-main {
      grid-template-columns: 1fr;
    }
    .ide-right-panel {
      max-height: 300px;
    }
    .container-max {
      padding-left: 20px;
      padding-right: 20px;
    }
