    }
    h2 {
      font-size: clamp(32px, 8vw, 56px) !important;
    }
    h3 {
      font-size: clamp(24px, 6vw, 40px) !important;
    }
    .lifecycle-card,
    .evidence-row,
    .governance-section,
    .principle-card {
      padding: 20px;
    }
    .mega-dropdown-content {
