    }
    .section-padding {
      padding: 60px 0;
    }
    .grid.md\:grid-cols-3,
    .grid.lg\:grid-cols-3 {
      grid-template-columns: 1fr !important;
    }
    .grid.md\:grid-cols-2,
    .grid.lg\:grid-cols-2 {
      grid-template-columns: 1fr !important;
    }
    .text-\[20px\],
    .text-\[24px\] {
      font-size: 18px;
    }
    .signal-row,
    .improvement-card {
      margin-bottom: 16px;
    }
