      padding-left: 32px;
      padding-right: 32px;
    }
  }
  @media (max-width: 640px) {
    .full-bleed {
      padding-left: 20px;
      padding-right: 20px;
    }
    .dashboard-stats {
      grid-template-columns: 1fr;
    }
    .heatmap-grid-dashboard {
      grid-template-columns: 1fr;
    }
